import { FC } from 'react'

import orderImage from '../../images/orderImage.svg'

import styles from './order-details.module.css'

interface IOrder {
  number: number;
}

const OrderDetails: FC<IOrder> = ({number}) => {
  return (
    <div className={styles.order} data-cy="orderDetails">
      <div className={styles.order_header}>
        <p className={`${styles.order_number} text text_type_digits-large`}>{number}</p>
        <p className='text text_type_main-medium'> </p>
      </div>
      <img className={styles.order_image} src={orderImage} alt=" " />
      <div className={`${styles.order_status}`}>
        <p className='text text_type_main-default'>   </p>
        <p className='text text_type_main-default text_color_inactive'>    </p>
      </div>
    </div>
  )
}

export default OrderDetails